<template>
  <div id="container">
    <!-- <button id="close" @click="closeVedio">close</button> -->
    <div id="box" >
      <div class="circle left">
        <div class="circleleft"></div>
      </div>
      <div class="circle right">
        <div class="circleright"></div>
      </div>
      <div id="vedioContaniner">
      <video id="video"></video>
      </div>
    </div>
    <p>屏幕即将闪烁，请保持姿势不变</p>
    <canvas id="canvas"  style="display:none"></canvas>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mediaStreamTrack: null,
      containerWidth: null,
      containerHeight: null,
      pictureUrl: null
    };
  },
  created() {
    this.$nextTick(() => {
      // this.startVedio();
      this.getValue();
      this.startVedio();

      setTimeout(() => {
        this.createPicture();
        this.closeVedio();
      }, 5000)
    });

  },
  methods: {
    startVedio() {
      let that = this;
      // 老的浏览器可能根本没有实现 mediaDevices，所以我们可以先设置一个空的对象
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }

      // 一些浏览器部分支持 mediaDevices。我们不能直接给对象设置 getUserMedia
      // 因为这样可能会覆盖已有的属性。这里我们只会在没有getUserMedia属性的时候添加它。
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
          // 首先，如果有getUserMedia的话，就获得它
          var getUserMedia =
            navigator.webkitGetUserMedia || navigator.mozGetUserMedia;

          // 一些浏览器根本没实现它 - 那么就返回一个error到promise的reject来保持一个统一的接口
          if (!getUserMedia) {
            return Promise.reject(
              new Error("getUserMedia is not implemented in this browser")
            );
          }

          // 否则，为老的navigator.getUserMedia方法包裹一个Promise
          return new Promise(function (resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        };
      }

      navigator.mediaDevices
        .getUserMedia({ video: { width: 1000, height: 1000 } })
        .then(function (stream) {
          var video = document.querySelector("video");
          that.mediaStreamTrack = stream;
          // 旧的浏览器可能没有srcObject
          if ("srcObject" in video) {
            video.srcObject = stream;
          } else {
            // 防止在新的浏览器里使用它，应为它已经不再支持了
            video.src = window.URL.createObjectURL(stream);
          }
          video.onloadedmetadata = function () {
            video.play();
          };
        })
        .catch(function (err) {
          console.log(err.name + ": " + err.message);
        });
    },
    closeVedio() {
      console.log("close"); 
      this.mediaStreamTrack.getTracks()[0].stop();
      // this.mediaStreamTrack.getTracks()[1].stop();
    },
    getStyle() {
      let el = document.getElementById("container");
      console.log(el);
      if (window.getComputedStyle) {
        console.log("getComputedStyle");
        return window.getComputedStyle(el, null);
      } else {
        console.log("currentStyle");
        return el.currentStyle;
      }
    },
    getValue() {
      var obj = this.getStyle();

      console.log("height:", obj.height);
      console.log("width:", obj.width);
      this.containerWidth = obj.width;
      this.containerHeight = obj.height;
      let box = document.getElementById("box");
      // let box = document.getElementsByClassName("box");
      let circleleft = document.getElementsByClassName("circleleft");
      let circleright = document.getElementsByClassName("circleright");
      let circle = document.getElementsByClassName("circle");
      let video = document.getElementById("video");
      //画布dom
      let canvasNode = document.getElementById("canvas");
      
      console.log(circleleft);

      //设置好 main盒子的高度
      box.style.height = (parseFloat(this.containerWidth)/3)*2 + "px";
      //设置好 main盒子的宽度
      box.style.width = (parseFloat(this.containerWidth)/3)*2 + "px";

      circleleft[0].style.height = (parseFloat(this.containerWidth)/3)*2 + "px";
      circleleft[0].style.width = (parseFloat(this.containerWidth)/3)*2 + "px";

      circleright[0].style.height = (parseFloat(this.containerWidth)/3)*2 + "px";
      circleright[0].style.width = (parseFloat(this.containerWidth)/3)*2 + "px";

      circle[0].style.height = (parseFloat(this.containerWidth)/3)*2 + "px";
      circle[0].style.width = parseFloat(this.containerWidth)/3 + "px";
      circle[1].style.height = (parseFloat(this.containerWidth)/3)*2 + "px";
      circle[1].style.width = parseFloat(this.containerWidth)/3 + "px";

      video.style.height = (parseFloat(this.containerWidth)/3 )*2 + "px";
      video.style.width = (parseFloat(this.containerWidth)/3 )*2 + "px";

      //设置画布宽高
      canvasNode.style.height = (parseFloat(this.containerWidth)/3)*2 + "px";
      canvasNode.style.width = (parseFloat(this.containerWidth)/3)*2 + "px";

      //设置main盒子的margin
      box.style.marginTop = (parseFloat(this.containerHeight) - parseFloat(this.containerWidth)) / 2 + "px";
      box.style.marginLeft = parseFloat(this.containerWidth) / 6 + "px";
      console.log(
        "box.style.height:",
        box.style.height,
        "box.style.width:",
        box.style.width
      );
    },
    //作画，并生成照片base64编码
    createPicture(){
      var canvas = document.getElementById('canvas');
      var video = document.getElementById('video');
      var context = canvas.getContext("2d");
      const width = (parseFloat(this.containerWidth)/3)*2;
      const height = (parseFloat(this.containerWidth)/3)*2;
      context.drawImage(video, 0, 0, width, height);
      this.pictureUrl = canvas.toDataURL().substr(22);
      // console.log(this.pictureUrl);
      console.log(this.pictureUrl);
    }
  },
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}
/* 绿色 蓝色 黄色 棕色 黑色 */
#container {
  width: 100%;
  height: 100%;
  background: blue;
  animation:blink 5s infinite ;
  -moz-animation:blink 5s infinite ;  /*火狐里面*/
  -webkit-animation:blink 5s infinite ;  /*谷歌和safair里面*/
  -o-animation:blink 5s infinite ; /*欧朋浏览器*/
  animation-delay: 1s;
  -webkit-animation-delay: 1s; 
  overflow: hidden;
}

p{
  text-align: center;
  font-size: 20px;
  color: white;
  margin-top: 10px;
}

/* #vedioContaniner{
  border-radius: 50%;
  overflow: hidden;
} */

#box {
  /* width: 400px; */
  /* height: 400px; */
  /* border: 1px solid black; */
  position: relative;
}

.circle {
  /* width: 200px; */
  /* height: 400px; */
  border: 1px solid transparent;
  position: absolute;
  overflow: hidden;
}

.left {
  left: 1px;
} 

 .right {
  right: 1px;
}

.circleleft {
  /* width: 400px; */
  /* height: 400px; */
  border: 10px solid transparent;
  position: absolute;
  border-bottom: 10px solid rgb(255, 255, 255, 0.4);;
  border-left: 10px solid rgb(255, 255, 255, 0.4);;
  border-radius: 50%;
  transform: rotate(45deg);
  animation: move1 3s infinite linear;
}

.circleright {
  /* width: 400px; */
  /* height: 400px; */
  border: 10px solid transparent;
  position: absolute;
  border-top: 10px solid rgb(255, 255, 255, 0.4);
  border-right: 10px solid rgb(255, 255, 255, 0.4);;
  border-radius: 50%;
  right: 0;
  transform: rotate(45deg);
  animation: move 3s infinite linear;
}

@keyframes move {
  0% {
    transform: rotate(45deg);
  }
  50%,
  100% {
    transform: rotate(225deg);
  }
}

@keyframes move1 {
  0%,
  50% {
    transform: rotate(45deg);
  }

  100% {
    transform: rotate(225deg);
  }
}

/*火狐里面*/
  @-moz-keyframes blink{
      0%, 25%{background: blue}
     26%, 50%{background: green}
     51%, 75%{background: yellow}
     76%{background: rgb(99, 99, 53)}
     100%{background: black}
  }
  /*谷歌和Safari里面*/
 @-webkit-keyframes blink{
      0%, 25%{background: blue}
     26%, 50%{background: green}
     51%, 75%{background: yellow}
     76%{background: rgb(99, 99, 53)}
     100%{background: black}
  }
 /*欧朋浏览器*/
  @-o-keyframes blink{
      0%, 25%{background: blue}
     26%, 50%{background: green}
     51%, 75%{background: yellow}
     76%{background: rgb(99, 99, 53)}
     100%{background: black}
  }

video {
  width: 50%;
  height: 50%;
  margin: 0 auto;
  background-color: black;
  display: block;
  border-radius: 50%;
  object-fit: fill;
  /* position: absolute; */
}
</style>